nav {
  padding-top: 1.6rem;
  top: 0;
  position: -webkit-sticky;
  position: sticky;
  place-items: center;
  z-index: 1;
  background-color: #E26A2C;
}
nav .logo-align {
  display: flex;
}
nav .logo-align .logo {
  color: white;
  letter-spacing: 0.6rem;
  font-weight: 300;
  font-size: 20px;
}
nav .toggle {
  display: none;
}
nav ul {
  text-align: right;
}
nav ul li {
  display: inline;
  padding-right: 0rem;
  padding-left: 3.2rem;
  padding-top: 3rem;
  font-style: normal normal 600 1.6rem/2.4rem Mulish;
  font-size: 1.6rem;
  font-weight: 600;
  letter-spacing: 0rem;
  opacity: 1;
  color: #FFFFFF;
}
nav ul li .counter {
  margin-top: -2%;
  margin-left: -4%;
  background-color: #FDA65D;
  width: 23px;
  height: 23px;
  float: right;
  padding: -7px 3px 0px 3px;
  border-radius: 50%;
  color: #FFFFFF;
  position: relative;
  text-align: center;
  font-weight: 500;
}
nav ul li .img-3 {
  width: 2.2rem;
  height: 2.4rem;
  padding-right: 0.8rem;
}
nav .toggle {
  padding-top: 1.8rem;
  padding-left: 1.8rem;
}

@media (min-width: 768px) and (max-width: 1023px) {
  .sign-in span {
    display: none;
  }
  .sign-in .img-2 {
    display: none;
  }
  nav ul li {
    padding-left: 2rem;
  }
}
@media (min-width: 320px) and (max-width: 767px) {
  nav .toggle {
    display: block;
  }
  nav ul li .counter {
    margin-top: -4%;
    margin-left: -7%;
  }
  .sign-in ul li span {
    display: none;
  }
  nav ul li .img-2 {
    display: none !important;
  }
  nav ul li .img-3 {
    width: 2.2rem;
    height: 2.4rem;
  }
  .main-menu {
    display: none;
  }
}
.rectangle {
  height: 0.4rem;
