.filter-container {
  display: block;
  position: relative;
  padding-left: 3.5rem;
  margin-bottom: 1.2rem;
  cursor: pointer;
  font-size: 2.2rem;
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
}

.filter-container input {
  position: absolute;
  opacity: 0;
  cursor: pointer;
  height: 0;
  width: 0;
}

.checkmark {
  position: absolute;
  top: 0;
  left: 0;
  height: 2.4rem;
  width: 2.4rem;
  background-color: #FFFFFF;
  border: 0.2rem solid #91959C;
  border-radius: 0.3rem;
}

.container:hover input ~ .checkmark {
  background-color: #FFFFFF;
}

.container input:checked ~ .checkmark {
  background-color: #FFFFFF;
  border: 2rem solid #1F39FF;
  border-width: 0.2rem;
}

.checkmark:after {
  content: "";
  position: absolute;
  display: none;
}

.filter-container input:checked ~ .checkmark:after {
  display: block;
}

.filter-container .checkmark:after {
  left: 1.3rem;
  top: -0.7rem;
  width: 0.4rem;
  height: 2rem;
  border: solid #1F39FF;
  z-index: 2;
  border-width: 0 0.2rem 0.2rem 0;
  transform: rotate(45deg);
}

